import React from "react"
import { Flex, Text, Box, Image } from "rebass"
import Column from "../column"

const Title = props => (
	<Flex>
		{props.titles.map((title, key) => (
			<Column key={`${title}-${key}`}>{title}</Column>
		))}
	</Flex>
)
export const TableLine = props => (
	<Flex>
		{props.children}
		<Column>
			<Image src="/5/edit.png" />
		</Column>
		<Column>
			<Image src="/5/1.png" />
		</Column>
		<Column>
			<Image src="/5/2.png" />
		</Column>
		<Column>
			<Image src="/7/bigger.png" />
		</Column>
	</Flex>
)
export default props => {
	return (
		<Flex
			sx={{
				cursor: "pointer",
				background: "#FFF0E5"
			}}
		>
			<Title titles={props.titles} />
			<Box style={{ margin: "0 18px 18px 18px" }}>{props.children}</Box>
		</Flex>
	)
}
